* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Comfortaa", cursive;
  background: rgb(63, 94, 251);
  background: linear-gradient(
    90deg,
    rgba(63, 94, 251, 1) 0%,
    rgba(252, 70, 107, 1) 100%
  );
}

.app {
  max-width: 1200px;
  max-height: 100vh;
  margin: auto auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 100px;
}

.info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  text-shadow: 2px 2px black;
}

.avatar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 60px;
}

.avatar {
  width: 200px;
  border-radius: 50%;
}

.heart-main {
  color: white;
  font-size: 60px;
  animation: pulse 2s linear infinite;
}

@keyframes heartColorChange {
  from {
    color: white;
  }
  to {
    color: red;
  }
}

@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 0;
    color: white;
  }
  10% {
    transform: scale(1.3);
  }
  20% {
    transform: scale(0.9);
  }
  30% {
    transform: scale(1.2);
  }
  40% {
    transform: scale(0.9);
  }
  50% {
    transform: scale(1.3);
    opacity: 0.5;
  }
  60% {
    transform: scale(0.95);
  }
  70% {
    transform: scale(1);
  }
  100% {
    transform: scale(1);
    opacity: 1;
    color: red;
  }
}

.greeting {
  font-size: 30px;
  margin-top: 8px;
}

.feelings {
  margin-top: 10px;
  margin-left: 20px;
  margin-right: 20px;
  text-align: justify;
}

.btns-container {
  margin-top: 20px;
}

.button {
  padding: 20px 30px;
  background: rgb(238, 174, 202);
  background: linear-gradient(
    90deg,
    rgba(238, 174, 202, 1) 0%,
    rgba(148, 187, 233, 1) 100%
  );
  border-radius: 10px;
  font-size: 20px;

  cursor: pointer;
}

.button:hover {
  background: #536976;
  background: -webkit-linear-gradient(to right, #292e49, #536976);
  background: linear-gradient(to right, #292e49, #536976);
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  color: white;
}

.button-yes {
  padding: 20px 40px;
  margin-right: 20px;
}

.button-no {
  margin-left: 20px;
}

/* .button-no:hover {
  display: none;
} */

.heart {
  color: red;
}

.heart-broken {
}

.mistake {
  color: white;
  text-shadow: 2px 2px black;
  margin-top: 20px;
  font-weight: 400;
  font-size: 16px;
}

.about {
  margin-top: 100px;
  color: white;
  text-shadow: 2px 2px black;
  text-align: center;
  font-size: 14px;
}

audio {
  height: 40px;
  margin-top: 20px;
}

/* Modal */
.modal {
  display: flex;
  position: fixed;
  width: 100vw;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  justify-content: center;
  align-items: center;
  margin-top: -105px;
}

.modal.disabled {
  display: none;
}

.window {
  width: 80%;
  height: 400px;
  background-color: white;
  border-radius: 10px;
  padding: 30px;
}

.window-heading {
}

.window-text {
  margin-top: 10px;
  font-size: 13px;
}

#window-input {
  margin-top: 10px;
  width: 100%;
  height: 40px;
  resize: none;
}

.button-confirm {
  margin-top: 30px;
  font-size: 14px;
  padding: 10px 20px;
}

.disabled {
  display: none !important;
}

.thankyou {
  display: flex;
  flex-direction: column;
}

.thankyou-text {
  margin-top: 8px;
}

.window-warning {
  margin-top: 8px;
  font-weight: 600;
  font-size: 16px;
  color: red;
}

.button-go-home {
  margin-top: 10px;
}

.private-message-header {
  margin-top: 8px;
  font-weight: 600;
}

.private-message {
  /* overflow: hidden; */
  font-size: 12px;
  word-wrap: break-word;
}
